<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process21"></a>

<p><br></p>
<p><span class="rvts17">伊凡·本美兹（Ifan·Ben-Mezed）:</span></p>
<p><img width="508" height="618" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber69.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">伊万曾经是一名出色的雇佣军，然而在一场拯救精灵营地的战斗中，他却被雇主无情的出卖。</span>
<br><span class="rvts21">没人能毫发无损地逃脱寂寞狼群的袭击，因他要为自由付出沉重的代价。</span>
<br><span class="rvts21">伊万曾发誓说已经做到了，但他又总会找出最后的美中不足。现在是时间反杀自己的雇主了。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族：人类</span>
<br><span class="rvts21">默认职业：旅人（</span><span class="rvts20">Wayfarer</span><span class="rvts21">）</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">伊万拥有人族的起始天赋。另外，伊万拥有系统默认的动物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）天赋（前提是玩家没有使用伊万作为主角，而是队友）。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">伊万只能被选为男性，并附带伊万</span><span class="rvts20"></span><span class="rvts21">本美兹特有标签。该标签可以使得他拥有一些特定的对话和行动。另外他还具备士兵和法外之徒的标签。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="107" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="294" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="589" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="107" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber70.jpg"></p>
</td>
<td width="294" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Encourage</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">鼓舞</span></p>
</td>
<td width="589" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">增加队友所有属性值。</span>
<br><span class="rvts21">随着队友等级越高，增加的数值也会越高。</span></p>
</td>
</tr>
<tr valign="top">
<td width="107" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber71.jpg"></p>
</td>
<td width="294" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Summon Ifan's Soul wolf</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">召唤灵魂之狼</span></p>
</td>
<td width="589" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">召唤战狼为角色战斗，战狼等级取决于伊凡的等级</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">配合着独有的种族优势，伊万可以召唤他的战狼，因此强烈建议把他作为一个远程攻击职业。</span>
<br><span class="rvts20">2. </span><span class="rvts21">由于伊万的战狼属性基于他的召唤系技能等级，因此建议他侧修召唤系技能。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



